import xiannv from "@/assets/xiannv.png";
import icon from "@/assets/titleIcon.png";
// import stateInfo from "../order/stateInfo";
import xiaobi from "@/assets/xiaobi.png";
import loading from "@/assets/loading.gif";
import Copy from "@/assets/copy.png";
import jiant from "@/assets/jiant.png";
import Clipboard from "clipboard";
import confirmPayment from "../component/confirmPayment";
export default {
  data() {
    return {
      isConfirmPayment: false,
      CopyIcon: Copy,
      jiant:jiant,
      isWaiting: true,
      showPositionValue: false, //提示信息
      toastContent: "",
      isShow: true,
      loading: loading,
      xiannvIcon: xiannv,
      xiaobiIcon: xiaobi,
      icon: icon,
      topTitle: "订单详情",
      infotitle: "订单信息",
      orderState: "订单状态",
      orderId: "订单编号",
      orderDate: "下单时间",
      expireTime: "到期时间",
      orderDetail: "订单明细",

      goods: "商品类型",
      meal_type: "套餐类型",
      contract_cycle: "套餐周期",
      price: "单份价格",
      buyNum: "购买数量",
      suanli: "算力大小",

      adValServices: "增值服务",
      power_rate: "电费",
      service: "矿机管理费率",
      taoBao: "套保服务费率",
      mingSpecifications: "矿机规格",
      mingtype: "类型",
      actualPower: "功耗",

      taoBaoVal: "套期保值",
      recovery_price: "回收价格",
      hedging_cycle: "服务周期",
      hedging_start_time: "服务起止时间",

      buy_total: "订单总额",
      pay_total: "实付款",
      min: "00",
      second: "00",
      list: {
        pay_status: "",
        add_time: "",
        order_id:0,
        expire_time: "",
        buy_total: "",
        pay_total: "",
        goods_name: "",
        meal_type: "",
        contract_cycle: "",
        price: "",
        buy_num: "",
        power_rate: "",
        service: "",
        mining_model: "",
        actual_power: "",
        pay_address: "",
        recovery_price: "",
        hedging_cycle: "",
        hedging_start_time: "",
        hedging_end_time: ""
      }
    };
  },
  filters: {
    uppercase: function(value) {
      if (!value) {
        return "";
      }
      return value.toUpperCase();
    }
  },
  components: {
    // stateInfo
    confirmPayment
  },
  mounted() {
    this.getdata();
  },
  methods: {
    getRouter() {
      this.$router.push("/myIncome");
    },
    getdata() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token,
          order_id: that.$route.params.order_id
        };
      that
        .$request({
          method: "POST",
          url:"/order/order-detail",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          console.log("响应")
          if (response.code == 0) {
            that.list = response.data;
            that.isShow = false;
            if (response.data.pay_status == 1) {
              that.getTime();
            }
          } else {
            that.isShow = false;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getTime() {
      var that = this,
        timer = setInterval(function() {
          var now = new Date();
          var end = new Date(that.list.add_time * 1000 + 30 * 60 * 1000);
          var result = parseInt(Math.floor(end - now) / 1000);
          if (result > 1) {
            result = result - 1;
            that.second = parseInt(Math.floor(result % 60)); // 计算秒 ，取余
            that.min = parseInt(Math.floor((result / 60) % 60));
          } else {
            clearInterval(timer);
            // that.disabled = true;
            that.isWaiting = false;
            // that.getdata();
          }
        }, 1000);
    },
    copyWord() {
      var that = this;
      var clipboard = new Clipboard(".CopyIcon");

      clipboard.on("success", function(e) {
        that.showPositionValue = true;
        that.toastContent = "复制成功";
        console.info("Text:", e.text);
        e.clearSelection();
      });

      clipboard.on("error", function(e) {
        console.error("Action:", e.action);
        console.error("Trigger:", e.trigger);
      });
    },
    //确认订单
    confirmOrder() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token,
          order_id: "404" //that.$route.params.order_id
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/order/confirm-order",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          that.showPositionValue = true;
          that.toastContent = response.errmsg;
          that.getdata();
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    // 线上余额支付-立即支付
    setPay() {
      this.isConfirmPayment = true;
    },
    isCloseFun() {
      this.isConfirmPayment = false;
    }
  }
};



